const state = {
  menuData: [],
  defaultKeys: []
};

const mutations = {
  SET_MENU: (state, data) => {
    state.menuData = data;
  },
  SET_DEFAULT_KEYS: (state, keys) => {
    state.defaultKeys = keys;
  }
};

const actions = {
  setMenu({ commit }, data) {
    commit("SET_MENU", data);
  },
  setDefaultKeys({ commit }, keys) {
    commit("SET_DEFAULT_KEYS", keys);
  }
};

export default {
  namespaced: false,
  state,
  mutations,
  actions
};
