import Cookies from "js-cookie";

const state = {
  size: "default"
};

const mutations = {
  SET_SIZE: (state, size) => {
    state.size = size;
    Cookies.set("size", size);
  }
};

const actions = {
  setSize({ commit }, size) {
    commit("SET_SIZE", size);
  }
};

export default {
  namespaced: false,
  state,
  mutations,
  actions
};
