import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

/**
 * 初始化路由实例
 * @returns {VueRouter}
 */
function initRouter(args = { mode: "hash" }) {
  const options = require("./config").default;
  args.routes = options.routes;
  return new Router({ ...args });
}

export default initRouter;
