import Vue from "vue";
import App from "./App";
import store from "./store";
import Antd from "ant-design-vue";
import "ant-design-vue/dist/antd.css";
import initRouter from "./router";
import initCharts from "./utils/initCharts";
import api from "./http/api";
import Cookies from "js-cookie";
// axios 拦截器
import "./http/axios";
import globalConfig from "./assets/config/global";
import "./public-path";

Vue.prototype.$api = api; // 对后端接口 进行全局注册
Vue.prototype.$baseImgUrl = globalConfig.baseImgUrl; //网站图片基础路径
Vue.prototype.$baseUrl = globalConfig.baseUrl; //网站接口基础路径
Vue.prototype.$Cookies = Cookies;

Vue.config.productionTip = false;
Vue.use(Antd);
Vue.use(initCharts);

let instance = null;
let router = null;

/**
 * 渲染函数
 * 两种情况：主应用生命周期钩子中运行 / 微应用单独启动时运行
 */
function render(props = {}) {
  router = initRouter({
    // base: window.__POWERED_BY_QIANKUN__ ? props.routerBase : "/",
    // mode: "history"
  });

  // 挂载应用
  instance = new Vue({
    router,
    store,
    render: h => h(App)
  }).$mount("#app");
}

// 独立运行时，直接挂载应用
if (!window.__POWERED_BY_QIANKUN__) {
  render();
}

/**
 * bootstrap 只会在微应用初始化的时候调用一次，下次微应用重新进入时会直接调用 mount 钩子，不会再重复触发 bootstrap。
 * 通常我们可以在这里做一些全局变量的初始化，比如不会在 unmount 阶段被销毁的应用级别的缓存等。
 */
export async function bootstrap() {
  // console.log("DashboardApp bootstraped");
}

/**
 * 应用每次进入都会调用 mount 方法，通常我们在这里触发应用的渲染方法
 */
export async function mount(props) {
  // console.log("DashboardApp mount", props);
  render(props);
}

/**
 * 应用每次 切出/卸载 会调用的方法，通常在这里我们会卸载微应用的应用实例
 */
export async function unmount() {
  // console.log("DashboardApp unmount");
  instance.$destroy();
  instance = null;
  router = null;
}
